* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin: 50px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li, ul{
  list-style: none;
  padding-left: 0;
  align-items: center;
  padding-bottom: 10px;
}
img{
  margin-right: 10px;
}
span{
  font-weight:700;
}
.name{
  font-size: 28px;
}
.item-card{
  display: flex;
}
.item__text{
  display: flex;
  align-items: center;
  margin-top: 0;
  margin-bottom: 10px;
}

.item__text-bold{
  margin-right: 10px;
}

.country-info,.country-list{
  transform: translateX(-200%);
  transition: all 500ms linear;
}

.js-active{
transform: translateX(0);
}